<template>
  <div>
    <v-card light class="mb-6">
      <v-card-title>
        {{ $t('comments.form.heading') }}
      </v-card-title>
      <v-card-text>
        <!-- Comment Form -->
        <comment-form :post="post" />
      </v-card-text>
    </v-card>
    <v-card light class="pb-6">
      <v-card-title>
        {{ post.comments.length }}
        {{
          post.comments.length === 1
            ? $t('comments.singular')
            : $t('comments.plural')
        }}
      </v-card-title>
      <div v-for="(cm, i) in getComments" :key="i" class="mx-5 my-6">
        <!-- Single Comment -->
        <single-comment :comment="cm" />
      </div>
      <v-btn
        v-if="getComments.length !== post.comments.length"
        color="primary"
        text
        small
        @click="
          limit += $store.state.settings.settings.blog.comments.commentsLimit
        "
        >{{ $t('comments.showMoreComments') }}</v-btn
      >
      <v-btn
        v-if="getComments.length > getCommentsLength"
        color="primary"
        text
        small
        @click="
          limit = $store.state.settings.settings.blog.comments.commentsLimit
        "
        >{{ $t('comments.showLessComments') }}</v-btn
      >
      <v-btn
        v-if="post.comments.length !== getComments.length"
        color="primary"
        text
        small
        @click="limit = post.comments.length"
        >{{ $t('comments.showAllComments') }}</v-btn
      >
    </v-card>
  </div>
</template>

<script>
export default {
  components: {
    CommentForm: () => import('@/components/blog/comments/CommentForm'),
    SingleComment: () => import('@/components/blog/comments/SingleComment')
  },
  props: {
    post: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      limit: this.$store.state.settings.settings.blog.comments.commentsLimit
    }
  },
  computed: {
    getCommentsLength() {
      return this.$store.state.settings.settings.blog.comments.commentsLimit
    },
    getComments() {
      return this.post.comments.slice(0, this.limit)
    }
  }
}
</script>

<style lang="scss" scoped></style>
